/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
list, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, list, nav, section {
  display: block; }

ol, ul {
  list-style: none; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

*, *::before, *::after {
  box-sizing: border-box; }

.header {
  background-color: #AA0601; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between; }
  @media (max-width: 992px) {
    .container {
      flex-direction: column;
      flex-wrap: wrap; } }

.logo {
  display: block;
  width: 80px;
  height: 50px;
  background-image: url(../images/logo-new_@2x.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden; }
  @media (max-width: 992px) {
    .logo {
      width: 100%; } }

.nav-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }
  @media (max-width: 992px) {
    .nav-list li {
      width: 50%;
      border: 1px solid #fff; } }
  .nav-list a {
    display: block;
    color: #fff;
    padding: 10px 0 10px 0;
    margin-left: 64px; }
    .nav-list a:hover {
      background-color: #650300; }
    @media (max-width: 992px) {
      .nav-list a {
        width: 100%;
        margin-left: 0;
        text-align: center; } }

.footer {
  background-color: #AA0601;
  color: #fff; }
  .footer .container {
    flex-direction: column;
    padding-top: 48px; }
    @media (max-width: 992px) {
      .footer .container {
        padding: 24px 16px; } }
  .footer a {
    color: #fff; }

.contact {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 992px) {
    .contact {
      margin-bottom: 24px; } }

.footer-nav-list {
  display: flex; }
  .footer-nav-list a {
    display: block;
    padding: 12px 0;
    margin-right: 48px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400; }
    .footer-nav-list a:hover {
      background-color: #650300; }
  @media (max-width: 992px) {
    .footer-nav-list {
      display: none; } }

.contact-list li {
  display: flex;
  align-items: center; }

.contact-list .icon-style2 {
  font-size: 36px;
  font-variation-settings: "wght" 700, "opsz" 36; }
  @media (max-width: 992px) {
    .contact-list .icon-style2 {
      font-size: 20px;
      font-variation-settings: "wght" 400, "opsz" 20; } }

.contact-list a {
  font-size: 36px;
  line-height: 54px; }
  @media (max-width: 992px) {
    .contact-list a {
      font-size: 16px;
      line-height: 24px; } }

.sns-list {
  display: flex; }
  .sns-list img {
    display: block;
    height: 48px;
    margin-left: 32px; }
    @media (max-width: 992px) {
      .sns-list img {
        height: 24px;
        margin-left: 8px; } }

.legal {
  border-top: 1px solid #fff;
  padding: 32px 0;
  display: flex;
  justify-content: space-between; }
  @media (max-width: 992px) {
    .legal p {
      margin-bottom: 8px; } }
  .legal .legal-list {
    display: flex; }
    .legal .legal-list li {
      margin-left: 48px; }
      @media (max-width: 992px) {
        .legal .legal-list li {
          margin-left: 0; } }
    .legal .legal-list a {
      display: block; }
    @media (max-width: 992px) {
      .legal .legal-list {
        flex-direction: column;
        gap: 8px 0; } }
  @media (max-width: 992px) {
    .legal {
      padding: 16px 0 0 0;
      flex-direction: column; } }

